<template>
  <div>
    <section>
      <h1 style="text-align : center">Q & A</h1>
      <form action="#" method="post">
        <div class="form-inline sms-form-center">
          <label class="mr-sm-2" for="title">Q&A </label>
          <input
            v-model.trim="title"
            class="form-control mb-2 mr-sm-2"
            type="text"
            name="title"
            placeholder="  "
          />
          <button @click.prevent="searchQna" class="btn btn-success mb-2 mr-sm-2" type="submit"></button>
        </div>
      </form>

      <div class="table-box">
        <table class="table table-striped table-hover table-bordered">
          <thead class="bg-secondary">
            <tr>
              <th></th>
              <th colspan="4"></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="qna in qnalist" v-bind:key="qna.qna_no" @click="getQna(qna.qna_no)">
              <td>{{qna.qna_no}}</td>
              <td colspan="4">{{qna.qna_title}}</td>
              <td>{{qna.qna_userid}}</td>
            </tr>
          </tbody>
        </table>

        <div v-if="name.length > 0" class="d-flex justify-content-end">
          <router-link class="btn btn-info mb-2 mr-sm-2" to="/addQna"></router-link>
        </div>
      </div>
    </section>
  </div>
  <!-- searchQna  
  hit count -->
</template>

	<script>
import http from "@/http-common.js";
import Constant from "@/Constant.js";

export default {
  data() {
    return {
      title: ""
    };
  },
  computed: {
    qnalist() {
      return this.$store.state.qnaItems;
    },
    name() {
      return this.$store.state.name;
    }
  },
  created() {
    /*
		http.get("/qna")
		.then(response => {this.searchlist = response.data;})
		.catch(error => {alert("Error: ", error);});
		*/
    this.$store.dispatch(Constant.GET_QNALIST);
  },
  methods: {
    searchQna() {
      console.log("click search");
      http
        .post("/qna/search/", {
          title: this.title
        })
        .then(response => {
          this.$store.commit(Constant.GET_QNALIST, { qnaItems: response.data });
        })
        .catch(err => {
          alert("Error : ", err);
        });
    },

    getQna(no) {
      console.log("getQna -> " + no);
      this.$router.push("/qna/" + no);
    }
    // ...mapActions([
    //     Constant.REMOVE_TODO,Constant.COMPLETE_TODO
    // ])
  }
};
</script>

<style scoped>
.content-box {
  text-align: center;
}

.table-box {
  width: 700px;
  margin: auto;
}

.sms-form-center {
  justify-content: center;
}

.table {
  margin-top: 20px;
  table-layout: fixed;
}

td {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
</style>

